import * as React from 'react';
import styled from 'styled-components';
import { CommonLine, commonLineMap, theme } from '@ali/we-design';
import { TLineStyle } from '@ali/we-ui';
import type { PanelRenderProps } from '@ali/4ever-plugin-quickinsert';
import { insertHr } from '../actions';

interface LineProps {
  lineStyle: TLineStyle;
  onClick: (lineStyle: TLineStyle, width: number) => void;
}

const LineWrapper = styled.div`
  width: 100%;
  padding: 10px;

  &:hover {
    background-color: ${() => theme.colorFill4};
  }
`;

const Line: React.FC<LineProps> = React.memo(props => {
  const { lineStyle, onClick } = props;
  const defaultSize = commonLineMap[lineStyle].defaultSize;

  const handleClick = React.useCallback((style, width) => {
    onClick(style, width);
  }, [onClick]);

  return (
    <LineWrapper onClick={() => handleClick(lineStyle, defaultSize)}>
      <CommonLine color="black" size={defaultSize} lineType={lineStyle as TLineStyle} />
    </LineWrapper>
  );
});

const QuickInsertPanel: React.FC<PanelRenderProps> = props => {
  const { controller, close } = props;

  const handleInsert = React.useCallback(
    ((lineStyle, width) => {
      close();
      controller.run('onAction', insertHr({ type: lineStyle, color: 'black', sz: width }));
    }),
    [close, controller],
  );

  return (
    <div>
      {Object.keys(commonLineMap).map(lineStyle => (
        <Line
          key={lineStyle}
          onClick={handleInsert}
          lineStyle={lineStyle as TLineStyle}
        />
      ))}
    </div>
  );
};

export default QuickInsertPanel;
